import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { GroupsService } from '../groups.service';
import { Group } from '../groups.model';

import { UserService, User } from '@server/user.service';

@Component({
  selector: 'app-groups-list',
  template: require('./group-list.component.html'),
  styles: [require('./group-list.component.scss')]
})
export class GroupsListComponent implements OnInit {
  private searchResults: User[] = [];
  private searchString: string;

  constructor(private groupsService: GroupsService,
    private userService: UserService,
    private router: Router,
    private route: ActivatedRoute) { }

  ngOnInit() {
  }

  addGroup() {
    this.groupsService
      .create('UnnamedGroup')
      .take(1)
      .subscribe((newGroup: Group) => {
        this.openWithId(newGroup._id);
      });
  }

  openGroup(index: number) {
    this.groupsService.items.subscribe(groups => {
      this.openWithId(groups[index]._id);
    });
  }

  private openWithId(id: string) {
    this.router.navigate(['../group', id], {relativeTo: this.route});
  }

  search() {
    this.userService.searchUser(this.searchString).subscribe(batch => {
      this.searchResults = batch.data;
    });
  }
}



