import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { GroupsService } from '../groups.service';
import { Group } from '../groups.model';

@Component({
  selector: 'app-group-detail',
  template: require('./group-detail.component.html')
})
export class GroupDetailComponent implements OnInit {
  private group: Group;
  private groupName = '';

  private setGroup(group: Group) {
    this.group = group;
    this.groupName = group.name;
  }

  constructor(private route: ActivatedRoute, private groupsService: GroupsService) { }

  ngOnInit() {
    this.route.params.flatMap(params => {
      return this.groupsService.get(params['id']);
    }).forEach((group: Group) => {
      this.setGroup(group);
    });
  }

  setGroupName(name: string) {
    this.groupsService.patch(this.group._id, { 'name': name });
  }
}



