import { Injectable } from '@angular/core';

import { Group } from './groups.model';

import { FeathersService } from '@server/feathers.service';

@Injectable()
export class GroupsService extends FeathersService<Group> {

  constructor() {
    super('groups');
  }

  create(name: string) {
    return super.create({
      'name': name
    });
  }

}



